import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import ShopClothes from '../views/ShopClothes.vue'
import Blog1 from '../components/AboutPage/Blog1.vue'
import Blog2 from '../components/AboutPage/Blog2.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/shop',
    name: 'shop',
    component: () => import(/* webpackChunkName: "about" */ '../views/Shop.vue')
  },
  {
    path: '/clothes',
    name: 'clothes',
    component: ShopClothes
  },
  {
    path: '/blog1',
    name: 'blog1',
    component: Blog1
  },
  {
    path: '/blog2',
    name: 'blog2',
    component: Blog2
  },



]



const router = createRouter({
  history: createWebHashHistory(),
  routes
})


export default router
